.card {
  padding: 18px 20px;
  display: flex;
  align-items: center;
}
.card:hover {
  cursor: pointer;
  color: var(--brand-color);
}
.card .icon {
  display: flex;
  padding: 6px;
  box-sizing: border-box;
  align-items: center;
  justify-content: left;
  margin-right: 16px;
  background: var(--brand-color-1);
  border-radius: 8px;
}
.card .content-container {
  line-height: 1.8;
}
.card .text {
  font-size: 12px;
}
.card .title {
  font-weight: 800;
}
.arrow {
  transition: all 0.1s linear;
}
.rotate180 {
  transform: rotate(180deg);
}
.bottom {
  border-bottom: 1px solid var(--border-level-1-color);
}
