.menu-container {
  position: relative;
  width: 260px;
  flex-shrink: 0;
  min-height: 100vh;
  box-sizing: border-box;
  background-color: var(--bg-color-container);
  box-shadow: var(--shadow-out-right);
}
.menu-container .menu-collapse-btn {
  position: fixed;
  top: 240px;
  z-index: 2;
  width: 24px;
  height: 24px;
  padding: 0;
  color: var(--text-color-secondary);
  background: var(--bg-color-container);
  border-radius: 100%;
  border: 1px solid var(--border-level-2-color);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
